import { Component, ElementRef, ViewChild } from '@angular/core';
import { environment } from '../../../../environments/environment';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { CommonModule } from '@angular/common';
import { FormBuilder, FormGroup, FormsModule, ReactiveFormsModule, Validators } from '@angular/forms';
declare var bootstrap: any;

@Component({
  selector: 'app-mo-m',
  standalone: true,
  imports: [CommonModule,FormsModule,ReactiveFormsModule],
  templateUrl: './mo-m.component.html',
  styleUrl: './mo-m.component.scss'
})
export class MoMComponent {
 users: any[] = [];
  searchText: string = '';
  pageSize: number = 5;
  currentPage: number = 1;
  previewUrl: string | ArrayBuffer | null = null;
  toastMessage: string = '';
  newsForm: FormGroup;
  base64Image: string | null = null;

  pagination = {
    total: 0,
    page: 1,
    limit: this.pageSize
  };

  @ViewChild('errorToast', { static: false }) errorToast!: ElementRef;

  constructor(private fb: FormBuilder, private http: HttpClient) {
    this.newsForm = this.fb.group({
      title: ['', [Validators.required]],        // required
      newsLink: ['', [Validators.required]],     // required
      description: ['', [Validators.required]],
      bannerImage: [null, [Validators.required]]   // ðŸ‘ˆ make image required
      // required
    });
  }

  ngOnInit(): void {
    this.fetchNews();
  }

  // âœ… Fetch news with pagination
  fetchNews(page: number = 1, limit: number = this.pageSize) {
    this.http.get<any>(`${environment.apiUrl}/news?page=${page}&limit=${limit}`).subscribe({
      next: (res) => {
        if (res.success && res.data?.data) {
          this.users = res.data.data;
          this.pagination = res.data.pagination;
          this.currentPage = page; // sync frontend with backend
        }
      },
      error: (err) => {
        console.error('Error fetching news:', err);
        this.showErrorToast('Failed to fetch news');
      }
    });
  }

  // âœ… Pagination controls
  get totalPages(): number {
    return Math.ceil(this.pagination.total / this.pagination.limit);
  }

  changePage(page: number) {
    if (page >= 1 && page <= this.totalPages) {
      this.fetchNews(page, this.pageSize);
    }
  }

  goPrevious() {
    this.changePage(this.currentPage - 1);
  }

  goNext() {
    this.changePage(this.currentPage + 1);
  }

  onPageSizeChange(event: any) {
    this.pageSize = +event.target.value;
    this.currentPage = 1;
    this.fetchNews(this.currentPage, this.pageSize);
  }

  // âœ… Toast methods
  showErrorToast(message: string): void {
    this.toastMessage = message;
    const toastElement = this.errorToast.nativeElement;
    toastElement.classList.remove('bg-success');
    toastElement.classList.add('bg-danger');
    const toast = new bootstrap.Toast(toastElement);
    toast.show();
  }

  showSuccessToast(message: string) {
    this.toastMessage = message;
    const toastElement = this.errorToast.nativeElement;
    toastElement.classList.remove('bg-danger');
    toastElement.classList.add('bg-success');
    const toast = new bootstrap.Toast(toastElement);
    toast.show();
  }

  // âœ… Submit news
  onSubmit() {
    if (this.newsForm.invalid) return;

    const formValue = this.newsForm.value;
    const token = localStorage.getItem('authToken');
    const headers = new HttpHeaders({
      'Authorization': `Bearer ${token}`,
      'Content-Type': 'application/json'
    });

    const payload = {
      title: formValue.title,
      newsLink: formValue.newsLink,
      description: formValue.description,
      bannerImage: formValue.bannerImage
    };

    this.http.post(`${environment.apiUrl}/news`, payload, { headers })
      .subscribe({
        next: (res) => {
          this.showSuccessToast('News submitted successfully!');
          this.newsForm.reset();
          this.previewUrl = null;
          this.base64Image = null;

          // Refresh after posting
          this.fetchNews(this.currentPage, this.pageSize);
        },
        error: (err) => {
          this.showErrorToast('Failed to submit news. Please try again.');
          this.showErrorToast(err.error.errors);
          // this.showErrorToast('Failed to submit Event');
        }

      });
  }

  // âœ… File preview + Base64 conversion
  onFileSelected(event: any): void {
    const file = event.target.files?.[0];
    if (!file) return;

    if (file.size > 5 * 1024 * 1024) {
      this.showErrorToast('File size exceeds 5MB. Please upload a smaller file.');
      return;
    }

    const reader = new FileReader();
    reader.onload = () => {
      this.previewUrl = reader.result;
      this.base64Image = reader.result as string;

      // âœ… update form control so it passes validation
      this.newsForm.patchValue({ bannerImage: this.base64Image });
      this.newsForm.get('bannerImage')?.updateValueAndValidity();
    };
    reader.readAsDataURL(file);
  }


  openImagePreviewModal(): void {
    const modalElement = document.getElementById('imagePreviewModal');
    if (modalElement) {
      const modal = new bootstrap.Modal(modalElement);
      modal.show();
    }
  }

  // âœ… Delete news
  deleteNews(id: string) {
    if (!confirm('Are you sure you want to delete this news item?')) return;

    const token = localStorage.getItem('authToken');
    const headers = new HttpHeaders({
      'Authorization': `Bearer ${token}`
    });

    this.http.delete(`${environment.apiUrl}/news/${id}`, { headers }).subscribe({
      next: () => {
        this.showSuccessToast('News deleted successfully!');
        // Remove deleted item from the table
        this.users = this.users.filter(user => user.id !== id);
      },
      error: (err) => {
        console.error('Delete failed:', err);
        this.showErrorToast('Failed to delete news. Please try again.');
      }
    });
  }
}
